/**
 * @asset(qxc.restful/*)
 */
qx.Class.define("qxc.restful.Init", {
  type: "singleton",
  extend: qx.core.Object,

  construct: function () {
    this.base(arguments);

    this.resourceLoader = qxc.require.Init.require(["restful"])
      .catch(e => { throw e; })
      .spread(
        function (restful) {
          this.setRestful(restful);

          return [restful];
        }.bind(this));
  },

  statics: {
    getApi: function (url) {
      var loader = qxc.restful.Init.getInstance().resourceLoader;
      return loader.catch(e => { throw e; })
        .spread(function (restful) {
          return restful(url);
        });
    }
  },

  properties:
    {
      restful: {
        event: "change:restful",
        nullable: true
      }
    },

  members:
    {
      resourceLoader: null,

      init: function () {
        var r = qxc.require.Init.getInstance();
        r.addResourceManagerPath("restful", "qxc.restful/js/restful.min.js");
      },

      initPretty: function () {
        var r = qxc.require.Init.getInstance();

        r.addResourceManagerPath("restful", "qxc.restful/js/restful.js");
      }
    }
});
